import React, { Component, Fragment } from 'react';

import '../styles.css';

// *******************************************************
// TOOLTIP RAIL
// *******************************************************
const railStyle = {
    position: 'absolute',
    width: '100%',
    height: 40,
    cursor: 'pointer',
    zIndex: 300,
  };
  
  const railCenterStyle = {
    position: 'absolute',
    width: '100%',
    height: 14,
    borderRadius: 7,
    cursor: 'pointer',
    pointerEvents: 'none',
    backgroundColor: 'rgb(155,155,155)',
  };
  
  export class TooltipRail extends Component {
    state = {
      value: null,
      percent: null,
    };
  
    onMouseEnter = () => {
      document.addEventListener('mousemove', this.onMouseMove);
    };
  
    onMouseLeave = () => {
      this.setState({ value: null, percent: null });
      document.removeEventListener('mousemove', this.onMouseMove);
    };
  
    onMouseMove = (e) => {
      const { activeHandleID, getEventData } = this.props;
  
      if (activeHandleID) {
        this.setState({ value: null, percent: null });
      } else {
        this.setState(getEventData(e));
      }
    };
  
    render() {
      const { value, percent } = this.state;
      const { activeHandleID, getRailProps } = this.props;
  
      return (
        <Fragment>
          {!activeHandleID && value ? (
            <div
              style={{
                left: `${percent}%`,
                position: 'absolute',
                marginLeft: '-11px',
                marginTop: '-35px',
              }}
            >
              <div className="tooltip">
                <span className="tooltiptext">Value: {value}</span>
              </div>
            </div>
          ) : null}
          <div
            style={railStyle}
            {...getRailProps({
              onMouseEnter: this.onMouseEnter,
              onMouseLeave: this.onMouseLeave,
            })}
          />
          <div style={railCenterStyle} />
        </Fragment>
      );
    }
  }

// *******************************************************
// RAIL
// *******************************************************
const railOuterStyle = {
  position: 'absolute',
  width: '100%',
  height: '100%',
  borderRadius: 7,
  cursor: 'pointer',
};

const railInnerStyle = {
  position: 'absolute',
  width: '100%',
  height: 14,
  borderRadius: 7,
  pointerEvents: 'none',
  backgroundColor: 'rgb(155,155,155)',
};

export const SliderRail = ({ getRailProps }) => {
  return (
    <>
      <div style={railOuterStyle} {...getRailProps()} />
      <div style={railInnerStyle} />
    </>
  );
};

// *******************************************************
// HANDLE COMPONENT
// *******************************************************

export const Handle = ({
  domain: [min, max],
  handle: { id, value, percent },
  disabled = false,
  getHandleProps,
}) => {
  return (
    <div>
      <div
        style={{
          left: `${percent}%`,
          position: 'absolute',
          transform: 'translate(-50%, -25%)',
          WebkitTapHighlightColor: 'rgba(0,0,0,0)',
          zIndex: 5,
          width: 28,
          height: 28,
          cursor: 'pointer',
          backgroundColor: 'none',
        }}
        {...getHandleProps(id)}
      />
      <div
        role="slider"
        aria-valuemin={min}
        aria-valuemax={max}
        aria-valuenow={value}
        style={{
          left: `${percent}%`,
          position: 'absolute',
          transform: 'translate(-50%, -25%)',
          zIndex: 2,
          width: 24,
          height: 24,
          borderRadius: '50%',
          boxShadow: '1px 1px 1px 1px rgba(0, 0, 0, 0.3)',
          backgroundColor: disabled ? '#666' : '#9BBFD4',
        }}>
            <div style={{ fontFamily: 'Roboto', fontSize: 11 }}>
                <p style={{textAlign: 'center', lineHeight: "24px" }}>{value}</p>
            </div>
      </div>
    </div>
  );
};

// *******************************************************
// TRACK COMPONENT
// *******************************************************


export const Track = ({
  source,
  target,
  getTrackProps,
  disabled = false,
}) => {
  return (
    <div
      style={{
        position: 'absolute',
        height: 14,
        zIndex: 1,
        backgroundColor: disabled ? '#999' : '#607E9E',
        borderRadius: 7,
        cursor: 'pointer',
        left: `${source.percent}%`,
        width: `${target.percent - source.percent}%`,
      }}
      {...getTrackProps()}
    />
  );
};

// *******************************************************
// TICK COMPONENT
// *******************************************************

export const Tick = ({
  tick,
  count,
  format = (d) => d,
}) => {
  return (
    <div>
      <div
        style={{
          position: 'absolute',
          marginTop: 14,
          width: 1,
          height: 5,
          backgroundColor: 'rgb(200,200,200)',
          left: `${tick.percent}%`,
        }}
      />
      <div
        style={{
          position: 'absolute',
          marginTop: 22,
          fontSize: 10,
          textAlign: 'center',
          marginLeft: `${-(100 / count) / 2}%`,
          width: `${100 / count}%`,
          left: `${tick.percent}%`,
        }}
      >
        {format(tick.value)}
      </div>
    </div>
  );
};