import { React, useState, useEffect } from "react";
import "../styles.css";

import Card from 'react-bootstrap/Card';
import Form from 'react-bootstrap/Form';

import { Col, Row } from 'react-bootstrap'

import { Link } from "react-router-dom";

import 'react-virtualized/styles.css';

import { LazyLoadComponent } from 'react-lazy-load-image-component';

import PaginationContainer from '../Components/PagniationContainer';
import { YearPicker } from "../Components/YearPicker";

let typesOptions = [
    "Alle",
    "Akvarel",
    "Collage",
    "Linoleumstryk",
    "Litografi",
    "Monotypi",
    "Tegning",
    "Tegning med oliekridt",
    "Træsnit",
    "Oliebillede",
    "Radering"
]

let canBeBorrowedOptions = [
    "",
    "Nej",
    "Ja"
]

let picturesPrSideOptions = [
    10,
    30,
    50,
    100
]

function MakeFilterSection(props){
    return(
        <Card className="filter-card">
            <Card.Body>
                <Card.Title>Søg</Card.Title>
                <Form onSubmit={e => e.preventDefault()}>
                    <Form.Group controlId="filter.title">
                        <Form.Label>Titel</Form.Label>
                        <Form.Control type="text" onChange={e => props.setTitle(e.target.value)}/>
                    </Form.Group>
                    <Form.Group controlId="filter.type">
                        <Form.Label>Type</Form.Label>
                        <Form.Control as="select" onChange={e => props.setType(e.target.selectedIndex)}>
                        <option>{typesOptions[0]}</option>
                        <option>{typesOptions[1]}</option>
                        <option>{typesOptions[2]}</option>
                        <option>{typesOptions[3]}</option>
                        <option>{typesOptions[4]}</option>
                        <option>{typesOptions[5]}</option>
                        <option>{typesOptions[6]}</option>
                        <option>{typesOptions[7]}</option>
                        <option>{typesOptions[8]}</option>
                        <option>{typesOptions[9]}</option>
                        <option>{typesOptions[10]}</option>
                        </Form.Control>
                    </Form.Group>
                    <Form.Group controlId="filter.canBeBorrowed">
                        <Form.Label>Kan blive lånt</Form.Label>
                        <Form.Control as="select" onChange={e => props.setCanBeBerrowed(e.target.selectedIndex)}>
                        <option>{canBeBorrowedOptions[0]}</option>
                        <option>{canBeBorrowedOptions[1]}</option>
                        <option>{canBeBorrowedOptions[2]}</option>
                        </Form.Control>
                    </Form.Group>
                    <Form.Group controlId="filter.yearSelection">
                        <Form.Label>Årstal</Form.Label>
                        {
                            /*
                            <Row>
                                <Col>
                                <Form.Control type="text" placeholder="Fra" onKeyPress={e => {
                                    // Only allow numbers
                                    var charCode = (typeof e.which == "undefined") ? e.keyCode : e.which;
                                    var charStr = String.fromCharCode(charCode);
                                    if(!/\d/.test(charStr)){
                                        e.preventDefault();
                                    }      
                                }} onChange={e => props.setMinYear(e.target.value === "" ? -1 : Number(e.target.value))}/>
                                </Col>
                                <Col>
                                <Form.Control type="text" placeholder="Til" onKeyPress={e => {
                                    // Only allow numbers
                                    var charCode = (typeof e.which == "undefined") ? e.keyCode : e.which;
                                    var charStr = String.fromCharCode(charCode);
                                    if(!/\d/.test(charStr)){
                                        e.preventDefault();
                                    }      
                                }} onChange={e => props.setMaxYear(e.target.value === "" ? -1 : Number(e.target.value))}/>
                                </Col>
                            </Row>
                            */
                        }
                        <YearPicker setYearRestriction={props.setYearRestriction} yearOptions={props.yearOptions}/>
                    </Form.Group>
                    <Form.Group controlId="filter.picturePrSide">
                        <Form.Label>Billeder pr. side</Form.Label>
                        <Row>
                            <Col>
                            <Form.Control as="select" onChange={e => props.setImagesPrSide(picturesPrSideOptions[e.target.selectedIndex])}>
                            <option>{picturesPrSideOptions[0]}</option>
                            <option>{picturesPrSideOptions[1]}</option>
                            <option>{picturesPrSideOptions[2]}</option>
                            <option>{picturesPrSideOptions[3]}</option>
                            </Form.Control>
                            </Col>
                            <Col>
                                <p className="text-muted">Total of {props.totalResults} results</p>
                            </Col>
                        </Row>
                    </Form.Group>
                </Form>
            </Card.Body>
        </Card>
    );
}

function MakePictureEntry(props){
  const title = props.picture.title;
  const desc = props.picture.shortDesc;
  const imageSrc = props.picture.image;
  const canBeBorrowed = props.picture.canBeBorrowed;

  return(
    <LazyLoadComponent threshold={500}>
        <Card>
            <Link to={`/picture/${props.picture.id}`} className="card-link-img">
                <Card.Img variant="top" src={`${process.env.PUBLIC_URL}/img/${imageSrc}`} />
            </Link>
            <Card.Body>
            <Card.Title>{title}</Card.Title>
            <Card.Text>
                {desc}
            </Card.Text>
            </Card.Body>
            <Card.Footer className={canBeBorrowed ? "bg-can" : "bg-cannot"}>
            <small className="text-muted">{canBeBorrowed ? "Kan blive lånt" : "Kan ikke blive lånt"}</small>
            </Card.Footer>
        </Card>
    </LazyLoadComponent>
  );
}

function MakeCardGroup(props){
    const [data, setData] = useState([]);
    const setTotalResults = props.setTotalResults;
    if(data.length === 0) {
        fetch(`${process.env.PUBLIC_URL}/data/pictures.json`).then(e => e.json()).then(e => {
            setData(e);
            const minYearOption = Number(e.reduce((prev, curr) => Number(prev.date) < Number(curr.date) ? prev : curr).date);
            const maxYearOption = Number(e.reduce((prev, curr) => Number(prev.date) > Number(curr.date) ? prev : curr).date);
            props.setYearOptions([minYearOption, maxYearOption]);
        });
    }

    const pictures = data.filter(picture => {
        const titleMatch = picture.title.toLowerCase().includes(props.searchTerms.title.toLowerCase());
        const typeMatch = props.searchTerms.type === 0 || typesOptions[props.searchTerms.type] === picture.type;
        const canBeBorrowedMatch = props.searchTerms.canBeBorrowed === 0 || Boolean(props.searchTerms.canBeBorrowed - 1) === picture.canBeBorrowed;
        const aboveMinYear = props.searchTerms.yearRestriction[0] <=  Number(picture.date);
        const underMaxYear = props.searchTerms.yearRestriction[0] === -1 || props.searchTerms.yearRestriction[1] >= Number(picture.date);
        return titleMatch && typeMatch && canBeBorrowedMatch && aboveMinYear && underMaxYear;
    }).map(picture => {
        return (<MakePictureEntry key={picture.id} picture={picture}/>);
    })

    useEffect(_ => {
        setTotalResults(pictures.length)
    }, [pictures.length, setTotalResults]);

    if(pictures.length === 0){
        return (<h1>Ingen billeder blev fundet for de givne kriterier.</h1>);
    }

    return(
        <PaginationContainer elements={pictures} imagesPrSide={props.imagesPrSide}/>
    );
}


export default function Gallery() {
    const [title, setTitle] = useState("");
    const [type, setType] = useState(0); // Is an index
    const [canBeBorrowed, setCanBeBerrowed] = useState(0); // Is an index
    const [yearRestriction, setYearRestriction] = useState([-1, -1]);
    const [yearOptions, setYearOptions] = useState([0, Number.MAX_VALUE]);
    const [imagesPrSide, setImagesPrSide] = useState(picturesPrSideOptions[0]);
    const [totalResults, setTotalResults] = useState(0);
    const searchTerms = {title:title, type:type, canBeBorrowed:canBeBorrowed, yearRestriction:yearRestriction};

    return (
        <div className="Gallery mx-auto" style={{ maxWidth: '60%'}}>
            <Row>
                <Col>                 
                    <MakeFilterSection setTitle={setTitle} setType={setType} setCanBeBerrowed={setCanBeBerrowed} setImagesPrSide={setImagesPrSide} totalResults={totalResults}
                        setYearRestriction={setYearRestriction} yearOptions={yearOptions}/>
                </Col>
                <Col>
                    <MakeCardGroup searchTerms={searchTerms} setTotalResults={setTotalResults} setYearOptions={setYearOptions} imagesPrSide={imagesPrSide}/>
                </Col>
            </Row>
        </div>
    );
}
