import React, { useEffect, useState } from "react";

import ScrollToTopImage from "../img/MoveToTop.png"

export default function ScrollToTop() {
  const [isVisible, setIsVisible] = useState(false);

  // Show button when page is scrolled to given distance
  const toggleVisibility = () => {
    if (window.pageYOffset > 10) {
      setIsVisible(true);
    } else {
      setIsVisible(false);
    }
  };

  // Set the top cordinate to 0
  // make scrolling smooth
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    });
  };

  useEffect(() => {
    window.addEventListener("scroll", toggleVisibility);
  }, []);

  return (
    <div>
        <div onClick={scrollToTop} className= {"scroll-to-top" + (isVisible ? "" : " hidden")}>
          <img src={ScrollToTopImage} width="40" height="40" alt='Go to top'/>
        </div>
    </div>
  );
}
