import React, { Component, useEffect } from 'react';
import { Slider, Rail, Handles, Tracks } from 'react-compound-slider';
import { SliderRail, Handle, Track } from './SliderComponents';

const sliderStyle = {
  position: 'relative',
  width: '100%',
};

export class YearPicker extends Component {
  state = {
    values: this.props.yearOptions,
    reversed: false,
  };

  onChange = (values) => {
    this.props.setYearRestriction(values);
    this.setState({ values });
  };

  toggleReverse = () => {
    this.setState(prev => ({ reversed: !prev.reversed }));
  };


  render() {
    const {
      state: {values, reversed },
    } = this;

    return (
      <div style={{ height: "25px", marginBottom: ".5rem", marginTop: ".5rem", width: '100%' }}>
        <Slider
          mode={1}
          step={1}
          domain={this.props.yearOptions}
          reversed={reversed}
          rootStyle={sliderStyle}
          onUpdate={this.onUpdate}
          onChange={this.onChange}
          values={values}
        >
          <Rail>
            {({ getRailProps }) => <SliderRail getRailProps={getRailProps} />}
          </Rail>
          <Handles>
            {({ handles, getHandleProps }) => (
              <div className="slider-handles">
                {handles.map(handle => (
                  <Handle
                    key={handle.id}
                    handle={handle}
                    domain={this.props.yearOptions}
                    getHandleProps={getHandleProps}
                  />
                ))}
              </div>
            )}
          </Handles>
          <Tracks left={false} right={false}>
            {({ tracks, getTrackProps }) => (
              <div className="slider-tracks">
                {tracks.map(({ id, source, target }) => (
                  <Track
                    key={id}
                    source={source}
                    target={target}
                    getTrackProps={getTrackProps}
                  />
                ))}
              </div>
            )}
          </Tracks>
        </Slider>
      </div>
    );
  }
}